___CSS_LOADER_EXPORT___.push([module.id, `.colorblind-tester {
    padding: 16px 0;
}

.colorblind-controls {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    margin-bottom: 24px;
}

.control-group {
    flex: 1;
    min-width: 200px;
}

.select-control,
.range-control {
    width: 100%;
    padding: 8px;
    margin-top: 4px;
    border-radius: 4px;
    border: 1px solid #ced4da;
}

.colorblind-preview {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 24px;
}

.preview-container {
    flex: 1;
    min-width: 250px;
}

.color-samples {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    gap: 12px;
}

.color-sample {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 12px;
    height: 60px;
    border-radius: 4px;
    color: white;
    font-size: 12px;
    text-shadow: 0 0 2px rgba(0,0,0,0.7);
}

.colorblind-upload {
    margin-bottom: 24px;
}

.file-input {
    margin: 10px 0;
}

.image-preview {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.preview-original,
.preview-simulation {
    flex: 1;
    min-width: 250px;
}

#original-image-preview img,
#simulation-canvas {
    width: 100%;
    height: auto;
    border: 1px solid #ced4da;
    border-radius: 4px;
    display: block;
}

.colorblind-info {
    background: #f8f9fa;
    padding: 16px;
    border-radius: 8px;
    border: 1px solid #e9ecef;
}

.colorblind-info h4 {
    margin-top: 0;
    margin-bottom: 12px;
    color: #343a40;
}

.colorblind-info ul {
    margin-left: 20px;
}

.colorblind-info li {
    margin-bottom: 8px;
    color: #495057;
}
