;IAChB,kBAAkB;IAClB,yBAAyB;IACzB,yBAAyB;AAC7B;AACA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,qCAAqC;AACzC;AACA;IACI,sBAAsB;IACtB,eAAe;AACnB;;AAEA,iBAAiB;AACjB;IACI,eAAe;IACf,aAAa;IACb,sBAAsB;IACtB,gBAAgB;AACpB;AACA;IACI,eAAe;IACf,kBAAkB;IAClB,cAAc;AAClB;;AAEA,gBAAgB;AAChB;IACI,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,qCAAqC;AACzC;;AAEA,qBAAqB;AACrB;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA,aAAa","sourcesContent":["/* panel.css */\r\n\r\n/* ===== 기본 패널 스타일 ===== */\r\n#udl-accessibility-panel {\r\n    position: fixed;\r\n    top: 20px;\r\n    right: 20px;\r\n    width: 600px;\r\n    max-width: 90vw;\r\n    height: 90vh;\r\n    min-width: 300px;\r\n    min-height: 300px;\r\n    background: white;\r\n    border-radius: 8px;\r\n    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);\r\n    display: flex;\r\n    flex-direction: column;\r\n    overflow: hidden;\r\n    resize: both;\r\n    z-index: 99999;\r\n    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\r\n    font-size: 14px;\r\n    line-height: 1.5;\r\n    color: #212529;\r\n}\r\n\r\n.panel-header {\r\n    padding: 16px 20px;\r\n    background: linear-gradient(135deg, #3a7bd5, #2c5282);\r\n    color: #fff;\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    cursor: move;\r\n}\r\n\r\n.panel-title {\r\n    font-size: 16px;\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 8px;\r\n}\r\n\r\n.panel-controls {\r\n    display: flex;\r\n    gap: 8px;\r\n}\r\n\r\n.panel-btn {\r\n    background: none;\r\n    border: none;\r\n    color: white;\r\n    width: 24px;\r\n    height: 24px;\r\n    font-size: 18px;\r\n    border-radius: 4px;\r\n    cursor: pointer;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    transition: background 0.2s;\r\n}\r\n\r\n.panel-btn:hover {\r\n    background: rgba(255, 255, 255, 0.2);\r\n}\r\n\r\n.panel-tabs {\r\n    display: flex;\r\n    background: #f8f9fa;\r\n    border-bottom: 1px solid #e9ecef;\r\n    padding: 0 4px;\r\n}\r\n\r\n.panel-tabs button {\r\n    flex: 1;\r\n    padding: 12px 8px;\r\n    background: none;\r\n    border: none;\r\n    cursor: pointer;\r\n    font-weight: 500;\r\n    color: #495057;\r\n    position: relative;\r\n    transition: all 0.2s;\r\n}\r\n\r\n.panel-tabs button:hover {\r\n    color: #3a7bd5;\r\n    background: rgba(58, 123, 213, 0.05);\r\n}\r\n\r\n.panel-tabs button.tab-active {\r\n    color: #3a7bd5;\r\n    font-weight: 600;\r\n}\r\n\r\n.panel-tabs button.tab-active::after {\r\n    content: '';\r\n    position: absolute;\r\n    bottom: 0;\r\n    left: 10%;\r\n    width: 80%;\r\n    height: 3px;\r\n    background: #3a7bd5;\r\n    border-radius: 3px 3px 0 0;\r\n}\r\n\r\n.tab-content {\r\n    flex: 1;\r\n    overflow-y: auto;\r\n    padding: 16px;\r\n    background: #fff;\r\n}\r\n\r\n.panel-footer {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    padding: 12px 16px;\r\n    background: #f8f9fa;\r\n    border-top: 1px solid #e9ecef;\r\n    gap: 8px;\r\n}\r\n\r\n.button {\r\n    display: inline-flex;\r\n    align-items: center;\r\n    gap: 6px;\r\n    padding: 8px 12px;\r\n    border-radius: 4px;\r\n    border: none;\r\n    font-weight: 500;\r\n    font-size: 13px;\r\n    cursor: pointer;\r\n    transition: background 0.2s;\r\n}\r\n\r\n.primary-btn {\r\n    background: #3a7bd5;\r\n    color: white;\r\n}\r\n\r\n.primary-btn:hover {\r\n    background: #2c5282;\r\n}\r\n\r\n.secondary-btn {\r\n    background: #e9ecef;\r\n    color: #495057;\r\n}\r\n\r\n.secondary-btn:hover {\r\n    background: #ced4da;\r\n}\r\n\r\n/* ===== 가이드라인/카테고리 스타일 ===== */\r\n.category-section {\r\n    margin-bottom: 24px;\r\n}\r\n\r\n.category-section h3 {\r\n    margin-top: 0;\r\n    margin-bottom: 12px;\r\n    font-size: 16px;\r\n    color: #343a40;\r\n    padding-bottom: 8px;\r\n    border-bottom: 1px solid #e9ecef;\r\n}\r\n\r\n.guideline-item {\r\n    margin-bottom: 12px;\r\n    padding: 12px;\r\n    border-radius: 6px;\r\n    background: rgba(233, 236, 239, 0.3);\r\n    border-left: 4px solid #6c757d;\r\n    transition: all 0.2s;\r\n    cursor: pointer;\r\n}\r\n\r\n.guideline-item.pass {\r\n    border-left-color: #40c057;\r\n    background: rgba(64, 192, 87, 0.05);\r\n}\r\n\r\n.guideline-item.fail {\r\n    border-left-color: #fa5252;\r\n    background: rgba(250, 82, 82, 0.05);\r\n}\r\n\r\n.guideline-item h4 {\r\n    margin: 0 0 6px 0;\r\n    font-size: 14px;\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 8px;\r\n}\r\n\r\n.guideline-item p {\r\n    margin: 0;\r\n    font-size: 13px;\r\n    color: #495057;\r\n}\r\n\r\n.item-highlight {\r\n    outline: 3px solid #ff922b !important;\r\n    outline-offset: 2px;\r\n}\r\n\r\n.loading {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    padding: 20px;\r\n    color: #6c757d;\r\n}\r\n\r\n.loading::after {\r\n    content: '';\r\n    width: 20px;\r\n    height: 20px;\r\n    margin-left: 10px;\r\n    border: 2px solid #ced4da;\r\n    border-radius: 50%;\r\n    border-top-color: #3a7bd5;\r\n    animation: spin 0.6s linear infinite;\r\n}\r\n\r\n@keyframes spin {\r\n    to { transform: rotate(360deg); }\r\n}\r\n\r\n\r\n/* AI 제안 */\r\n/* AI 제안 */\r\n/* AI 제안 */\r\n.ai-analysis-cards {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 14px;\r\n    padding: 14px;\r\n    font-size: 12px;\r\n    color: #495057;\r\n    background: #f8f9fa;\r\n}\r\n\r\n.section-card {\r\n    background: #ffffff;\r\n    border: 1px solid #dee2e6;\r\n    border-radius: 8px;\r\n    padding: 14px 16px;\r\n    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);\r\n}\r\n\r\n.section-card h1 {\r\n    font-size: 16px; /* 더 작게 */\r\n    margin-bottom: 10px;\r\n    color: #212529;\r\n    font-weight: 700;\r\n}\r\n\r\n.section-card h2 {\r\n    font-size: 14px;\r\n    margin-bottom: 8px;\r\n    color: #343a40;\r\n    font-weight: 600;\r\n}\r\n\r\n.section-card